@import "utils";
@import "blocks";

.main {
  &__content {
    display: grid;
    row-gap: 120px;
    padding-block: 120px;

    @include on-tablet {
      row-gap: 148px;
      padding-block: 148px;
      &:nth-last-child(1) {
        padding-bottom: 0;
      }
    }
  }

  &__section {
    &--titled {
      color: $c-titled;
      font-size: 13px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      letter-spacing: 3px;
      text-transform: uppercase;

      &--align--center {
        text-align: center;
      }

      &--align--left {
        text-align: left;
      }
    }

    &--title {
      color: $c-title;
      font-style: normal;
      font-weight: 600;

      &--align--center {
        text-align: center;
      }

      &--align--left {
        text-align: left;
      }
    }

    &--description {
      color: $c-description;
      font-family: "Open Sans", sans-serif;
      font-style: normal;
      line-height: 150%;

      &--align--center {
        text-align: center;
      }

      &--align--left {
        text-align: left;
      }
    }
  }
}
